import React, { useEffect, useState, } from 'react'
import ProductsItem from '../productsItem/ProductsItem'
import './productsScreen.scss'
import { colors } from '../../variables/variables.js'
import firstImg from '../../img/.png'
import secondImg from '../../img/py4u3x9Z--Y.jpg'
import thirdImg from '../../img/.png'
import { Link } from 'react-router-dom'
export default function ProductsScreen() {

   const [openedBox, setopenedBox] = useState(0);


   const productsList = [
      { id: 1, title: "  ", text: "    ,      .     .        .  ,   ,  ,  . ,   .     .", img: firstImg, color: colors.green, },
      { id: 2, title: " ", text: "     ,       .    , ,     .    .    , ,  ,   .", img: secondImg, color: colors.violet, },
      { id: 3, title: " ", text: "    ,   .        .    : ,  , , , ,   .      .", img: thirdImg, color: colors.yellow, },

   ]


   // setRandomAnimation()





   return (

      <div id='productsScreen' className='products-screen screen' >
         <div className="container ">
            <h2> </h2>
            <div className="products-container" id='productsContainer'>
               {productsList.map(product =>

                  <ProductsItem
                     key={product.id}
                     product={product}

                     setopenedBox={setopenedBox}
                     openedBox={openedBox}
                  />
               )}
            </div>
            <article className='products-article'>
               <p>            .       </p>

            </article>
            <span> <Link to="/about/reviews"></Link></span>


         </div>


      </div>

   )
}
