import React, { useRef } from 'react'
import OrderInput from '../input/OrderInput'

import './OrderBox.scss'
import OrderButton from '../buttons/OrderButton'

import OrderItems from './OrderItems'
import { formValidationIncorrect, phoneValidation } from '../../utils/validation'
import { useEffect } from 'react'
import axios from 'axios'
import { telegram as constants } from '../../variables/variables'

export default function OrderBox({ setScreen }) {

   const form = useRef(null)
   useEffect(() => {
      form.current.childNodes.forEach(item => {
         item.classList.add("order-input--dark")
      })
   }, [])

   const TOKEN = constants.TOKEN
   const CHAT_ID = constants.CHAT_ID
   const URL = constants.URL


   function sendForm(e) {
      let message = '';
      e.preventDefault()
      message += "<b>  </b> \n"
      message += ":  " + form.current.elements["name"].value + "\n";
      message += ":  " + form.current.elements["email"].value + "\n";
      message += ":  " + form.current.elements["tel"].value + "\n";
      message += " :  " + form.current.elements["delivery"].value + '\n';
      message += ": \n"

      let allPrice = 0;
      let k = 1;
      for (let i = 0; i < sessionStorage.length; i++) {


         if (sessionStorage.key(i).slice(0, 5) === "order") {
            let item = sessionStorage.getItem(sessionStorage.key(i))
            item = JSON.parse(item)
            message += `   #${k}. ${item.name} (id: ${item.id}) ${item.price}.  -  ${item.amount}. \n`
            allPrice += item.price * item.amount
            k++
         }
      }
      message += " :  " + allPrice + " ."




      if (form.current.elements["name"].value.length > 1 &&
         form.current.elements["email"].value.length > 1 &&
         form.current.elements["tel"].value.length > 1) {
         axios.post(URL, {
            chat_id: CHAT_ID,
            parse_mode: 'html',
            text: message
         })
         setScreen(1)
         sessionStorage.clear()

      } else {

         formValidationIncorrect(form.current)
      }



   }


   return (
      <div className='order-box'>
         <h2> </h2>
         <OrderItems />
         <h4>  </h4>
         <fieldset className="order-box__delivery">

            <input form="order-box__contact-form" type="radio" id="delivery--sdek" name='delivery' value='sdek' /> <label htmlFor="delivery--sdek">    ( 300 .   )</label><br />
            <input form="order-box__contact-form" type="radio" id="delivery--post" name='delivery' value='post' /> <label htmlFor="delivery--post">   ( 300 .   )</label>
         </fieldset>
         <h3>  </h3>
         <div className="order-box__warning">
            <span>,    .
            </span>  <br />
            <span>          .</span>
         </div>

         <form ref={form} className='order-box__contact-form' id='order-box__contact-form' >
            <OrderInput type="text" name="name" placeholder="" />
            <OrderInput type="email" name="email" placeholder="E-mail" />
            <OrderInput type="tel" name="tel" placeholder="Phone" validation={phoneValidation} />
         </form>
         <OrderButton link="/order/" text=" " onClick={sendForm} />
      </div>
   )
}
